import React from "react";
import { keyboardScrollIntoView } from "suplink-sdk";

//  控制输入框在移动设备上防止被键盘遮挡

class Demo extends React.Component {
  render() {
    return (
      <div style={{ height: 667, width: "100%", position: "relative", overflow: "overlay" }}>
        <div style={{ height: 110, background: "#775698" }}></div>
        <div style={{ height: 110, background: "#775698" }}></div>
        <div style={{ height: 110, background: "#775698" }}></div>
        <div style={{ height: 110, background: "#775698" }}></div>
        <div style={{ height: 110, background: "#775698" }}></div>
        <input
          style={{
            marginTop: 100,
          }}
        ></input>
      </div>
    );
  }
}

export default keyboardScrollIntoView()(Demo);
